import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { Row, Col, Card, Form, Menu, Icon, Divider } from 'antd';
import TableRegisterSave from './TableRegisterSave';
import TableRegisterNotSave from './TableRegisterNotSave';

@Form.create({ name: 'ListStudentRegistedPage' })
class ListStudentRegisterdPage extends Component {
  state = {};

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  handleClick = e => {
    this.setState({
      current: e.key,
    });
    if (e.key === 'enteringInfo') {
      console.log('render');
    }
  };

  render() {
    return (
      <Card title="Danh sách sinh viên đã đăng ký">
        <Row className="rowMargin">
          <Col span={24}>
            <Menu onClick={this.handleClick} selectedKeys={[this.state.current]} mode="horizontal">
              {/* <Menu.Item key="add">
                <BtnAddNew title="Thêm sinh viên tổ chức thi" />
              </Menu.Item> */}
              {/* <Menu.Item key="delete">
                <BtnDelete title="Xóa SV đăng ký" />
              </Menu.Item> */}
              <Menu.Item key="printerReport">
                <Icon type="printer" theme="twoTone" twoToneColor="#6699ff" />
                In báo cáo
              </Menu.Item>
              <Menu.Item key="exportExcel">
                <Icon type="file-excel" theme="twoTone" twoToneColor="#00cc00" />
                Xuất Excel
              </Menu.Item>
            </Menu>
          </Col>
        </Row>
        <Divider>Danh sách Lớp đã đăng ký</Divider>
        <Row>
          <TableRegisterSave />
        </Row>
        <Divider>Danh sách sinh viên đăng ký học lại</Divider>
        <Row>
          <div>
            <p>
              Tổng số SV: <b>3</b>
            </p>
            <TableRegisterNotSave />
          </div>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <ListStudentRegisterdPage {...props} />
  </Page>
));



