import React, { Component } from 'react';
import { Table } from 'antd';
import { setListStatisticalStudentRegisteredStudyAgain } from 'dump/data';

class TableRegisterSave extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'Ký hiệu',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Tên môn',
        dataIndex: 'nameCourse',
        key: 'nameCourse',
      },
      {
        title: 'Tên lớp học lại',
        dataIndex: 'classNameAgain',
        key: 'classNameAgain',
      },
      {
        title: 'Số tín chỉ',
        dataIndex: 'sotchl',
        key: 'totalStudent',
      },
      {
        title: 'Số SV',
        dataIndex: 'totalStudent',
        key: 'totalStudent',
      },
      {
        title: 'Đơn giá TC áp',
        dataIndex: 'price',
        key: 'price',
      },
      {
        title: 'Tổng tiền',
        dataIndex: 'totalPrice',
        key: 'totalPrice',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <div>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setListStatisticalStudentRegisteredStudyAgain}
          columns={this.columns}
        />
      </div>
    );
  }
}

export default TableRegisterSave;



