import React, { Component } from 'react';
import { Table } from 'antd';
import { setListStudentRegisterStudyAgain } from 'dump/data';
import { BtnAddNew, BtnDelete } from 'components/common/button';

class TableRegisterNotSave extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dayOfBirth',
        key: 'dayOfBirth',
      },
      {
        title: 'Giới tính',
        dataIndex: 'sex',
        key: 'sex',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Tên lớp học lại',
        dataIndex: 'classNameAgain',
        key: 'classNameAgain',
      },
      {
        title: 'Đơn giá TC áp',
        dataIndex: 'price',
        key: 'price',
      },
      {
        title: 'Ngày cập nhật TĐ',
        dataIndex: 'dateUpateDT',
        key: 'dateUpateDT',
      },
      {
        title: 'Ngày cập nhật TC',
        dataIndex: 'dateUpateTC',
        key: 'dateUpateTC',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: () => (
          <BtnDelete type="error" className="btnSmallAddInTable" onClick={this.showModal}>
            Xóa
          </BtnDelete>
        ),
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <div>
        <div style={{ float: 'right' }}>
          <BtnAddNew title="Thêm sinh viên tổ chức thi" />
        </div>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setListStudentRegisterStudyAgain}
          columns={this.columns}
        />
      </div>
    );
  }
}

export default TableRegisterNotSave;



